.TodoItem{
    margin: 0 auto;
    margin-bottom: 10px;
    height: 70px;
    border-radius: 70px;
    padding-left: 15px;
    padding-right: 15px;
    border: 1px solid black;
    display: flex;
    align-items:center;
    justify-content: space-between;
}
.TodoItem:hover{
    background-color: #f0f0f0;
}
.TodoItem:hover .ItemBtn{
    display: inline
}

.TodoItem .ItemBtn{
    display: none;
}
.ant-btn{
    margin-left: 10px;
}